.range {
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.scale_range {
  width: 300px;
  height: 140px;
  flex-flow: wrap;
  justify-content: space-between;
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: bold;
  display: flex;
}

.scale_range__value:after {
  content: "GB";
}

.scale_range__range {
  color: #34495e;
  -webkit-appearance: none;
  width: 300px;
  height: 16px;
  background-color: #bdc3c7;
  border-radius: 8px;
  outline: 0;
  margin: 0 auto;
  font-size: 20px;
  font-weight: bold;
  display: block;
  position: relative;
}

.scale_range__range:before {
  content: "0";
  position: absolute;
  bottom: -40px;
}

.scale_range__range:after {
  content: "1000";
  position: absolute;
  bottom: -40px;
  right: 0;
}

.scale_range__range::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 36px;
  height: 36px;
  cursor: pointer;
  background-color: #fff;
  border: 6px solid #5ccb0d;
  border-radius: 50%;
  transition: all .3s ease-in-out;
}

.scale_range__range::-webkit-slider-thumb:hover {
  background-color: #fff;
  border: 6px solid #285f02;
}

.scale_range__range::-webkit-slider-thumb:active {
  transform: scale(1.2);
}

@media screen and (min-width: 768px) {
  .range {
    flex-direction: row;
  }
}

